import React, { Component } from "react";

interface Props {
    producer: any;
}
interface State {}

class ProducerToConsumer extends Component<Props, State> {
    videoRef: HTMLVideoElement | null;

    constructor(props: Props) {
        super(props);
        this.videoRef = null;
    }

    componentDidMount = async () => {
        const { producer } = this.props;
        console.log("componentDidMount", producer.producerId, this.videoRef);
        const { consumer, stream } = await this.producerToConsumer(producer.producerId);
        if (this.videoRef) {
            console.log(stream);
            this.stopStream();
            this.videoRef.srcObject = stream;
        }
    };

    shouldComponentUpdate = (nextProps: Props) => {
        const { producer } = this.props;
        console.log("shouldComponentUpdate", producer.producerId !== nextProps.producer.producerId);
        if (producer.producerId !== nextProps.producer.producerId) {
            return true;
        } else {
            return false;
        }
    };

    componentDidUpdate = async (prevProps: Props) => {
        const { producer } = this.props;
        console.log("componentDidUpdate", producer.producerId !== prevProps.producer.producerId);
        if (producer.producerId !== prevProps.producer.producerId) {
            this.stopStream();
            const { consumer, stream } = await this.producerToConsumer(producer.producerId);
            if (this.videoRef) {
                this.videoRef.srcObject = stream;
            }
        }
    };

    componentWillUnmount = () => {
        const { producer } = this.props;
        this.stopStream();
        console.log("componentWillUnmount", producer.producerId);
    };

    stopStream = () => {
        if (this.videoRef) {
            const stream: any = this.videoRef.srcObject;
            if (stream) {
                const tracks = stream.getTracks();
                tracks.forEach((track: any) => {
                    track.stop();
                });
            }
        }
    };

    producerToConsumer = async (producerId: string) => {
        const consumer = await window.roomClient.getConsumerFromProducerId(producerId);
        console.log(consumer.track);
        const stream = new MediaStream();
        stream.addTrack(consumer.track);
        return {
            consumer,
            stream,
        };
    };

    setMainVideoSrc = () => {
        const mainVideoElement: any = document.getElementById("main-video-element");
        const p2mVideoElement: any = document.querySelectorAll(".producer-to-consumer");
        for (let index = 0; index < p2mVideoElement.length; index++) {
            const element = p2mVideoElement[index];
            element.classList.remove("active");
        }
        if (this.videoRef) {
            this.videoRef.classList.add("active");
        }
        if (mainVideoElement) {
            mainVideoElement.srcObject = this.videoRef?.srcObject;
        }
    };

    render() {
        return (
            <div className="wrapper-video ">
                <video
                    className={"producer-to-consumer"}
                    onClick={this.setMainVideoSrc}
                    id={this.props.producer.producerId}
                    autoPlay={true}
                    muted={true}
                    style={{
                        float: "left",
                        width: "100%",
                        height: "100%",
                        objectFit: "contain",
                    }}
                    ref={(ref) => {
                        this.videoRef = ref;
                    }}
                ></video>
            </div>
        );
    }
}

export default ProducerToConsumer;
