import React, { Component } from "react";

import { Layout, Spin } from "antd";
import HomeFooter from "../layouts/HomePage/HomeFooter";
import { connect } from "react-redux";
import { RoomClient } from "../module/RoomClientNew";
import { getMessage, setMessage } from "../reducers/messageReducer";
import { ILocalStreams } from "../models/reducers/localStreams";
import ModalSetting from "../components/Modal/ModalSetting";
import HomeMain from "../layouts/HomePage/HomeMain";
import ListUser from "../components/ListUser/ListUser";
import { getUserInRoom } from "../reducers/usersReducer";
import HomeSiderChat from "../layouts/HomePage/HomeSiderChat";
import ResizeObserver from "react-resize-observer";
import { roomData } from "../models/variables/roomData";
import ListUserCamera from "../components/ListUser/ListUserCamera";
const { Header, Content, Footer, Sider } = Layout;

interface Props {
    localStreams: Array<ILocalStreams>;
    siderActive: string;
    getUserInRoom: Function;
    getMessage: Function;
}
interface State { }

class HomePage extends Component<Props, State> {
    state = {
        loading: true,
        siderFull: false,
    };

    componentDidMount = () => {
        // getDevices();
        if (roomData) {
            try {
                const roomClient = new RoomClient(
                    roomData.roomName,
                    roomData.roomKey,
                    roomData.username,
                    async (roomClient: RoomClient) => {
                        console.log(roomClient);

                        await roomClient.init();
                        await roomClient.initSocketEvent();
                        window.roomClient = roomClient;
                        this.props.getMessage()
                        await this.setState({ loading: false });
                        await this.props.getUserInRoom();
                    }
                );
            } catch (err) {
                console.log(err);
            }
        } else {
            this.setState({
                loading: true,
            });
        }
    };

    render() {
        const { loading, siderFull } = this.state;
        const { siderActive } = this.props;
        if (loading) {
            return (
                <div
                    style={{
                        width: "100%",
                        height: "100vh",
                        display: "flex",
                        justifyContent: "center",
                        alignItems: "center",
                    }}
                >
                    <Spin size="large" tip="Loading..."></Spin>
                </div>
            );
        }
        return (
            <Layout id="home-page">
                <ResizeObserver
                    onResize={(rect) => {
                        if (rect.width < 769) {
                            this.setState({
                                siderFull: true,
                            });
                        } else {
                            this.setState({
                                siderFull: false,
                            });
                        }
                    }}
                />
                <Header style={{ display: "none" }} id="home-page-header"></Header>
                {
                    siderFull ?
                        <Layout className="layout-sub-smallview">
                            <Header className="header-smallview">
                                <ListUserCamera />
                            </Header>
                            <Layout style={{background: 'transparent'}}>
                                <Content id="home-page-content-smallview" >
                                    <HomeMain />
                                </Content>
                                <Sider width={siderActive === "chat" ? 360 : 0} id="home-page-chat-sider" >
                                    <HomeSiderChat />
                                </Sider>
                            </Layout>
                            <ModalSetting />
                        </Layout>
                        :
                        <Layout className="layout-sub">
                            <Content id="home-page-content" >
                                <HomeMain />
                            </Content>
                            <Sider width={siderActive === "chat" ? 360 : 0} id="home-page-chat-sider" >
                                <HomeSiderChat />
                            </Sider>
                            <Sider width={siderActive === "users" ? 360 : 0} id="home-page-users">
                                <ListUser />
                            </Sider>
                            <ModalSetting />
                        </Layout>
                }
                <Footer id="home-page-footer">
                    <HomeFooter />
                </Footer>
            </Layout>
        );
    }
}
const mapStateToProps = (state: any) => {
    return {
        localStreams: state.localStreams,
        siderActive: state.siderActive,
    };
};

const mapDispatchToProps = (dispatch: any) => {
    return {
        getUserInRoom: () => dispatch(getUserInRoom()),
        getMessage: () => dispatch(getMessage()),
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(HomePage);
