import React, { Component } from "react";
import { connect } from "react-redux";
import VideoComponent from "../../components/VideoComponent/VideoComponent";
import { ISrcVideo } from "../../models/reducers/listSrcVideo";

interface Props {
    listSrcVideo: Array<ISrcVideo>;
}
interface State { }

export class HomeMain extends Component<Props, State> {
    state = {};

    render() {
        const { listSrcVideo } = this.props;
        const video = listSrcVideo.filter((item) => item.type === "localUser");
        return (
            <div
                className="home-main"
                style={{
                    width: "100%",
                    height: "100%",
                    border: "1px solid orange",
                    padding: 5,
                    display: "flex",
                    justifyContent: "center",
                    alignItems: "center",
                    position: 'relative'
                }}
            >
                {
                    <video
                        poster="./images/poster.jpg"
                        id="main-video-element"
                        style={{ width: "100%", height: "100%", objectFit: "contain" }}
                        autoPlay={true}
                        muted={true}
                    ></video>
                }
                <div style={{ height: 25, lineHeight: '25px', background: 'transparent', position: 'absolute', bottom: 0, left: 0, padding: '0 5px' }}>
                    <span id="minutes-count"></span><span id="seconds-count"></span>
                </div>
            </div>
        );
    }
}

const mapStateToProps = (state: any) => ({
    listSrcVideo: state.listSrcVideo,
});

const mapDispatchToProps = {};

export default connect(mapStateToProps, mapDispatchToProps)(HomeMain);
