import { Dropdown, Tooltip, Menu, Badge, message } from "antd";
import React, { Component } from "react";
import { connect } from "react-redux";
import { DownOutlined, UserOutlined } from "@ant-design/icons";
import ResizeObserver from "react-resize-observer";
import { setModalSettingVisible } from "../../reducers/ui/modalSettingVisibleReducer";
import { setVideoOpen } from "../../reducers/ui/videoOpenReducer";
import { setMicOpen } from "../../reducers/ui/micOpenReducer";
import { setSiderActive } from "../../reducers/ui/siderActive";
import { ILocalStreams } from "../../models/reducers/localStreams";
import { ISrcVideo } from "../../models/reducers/listSrcVideo";
import { getConsumersFromServer } from "../../reducers/consumer";
import { closeLocalCamera, openLocalCamera, openMic, openShareScreen } from "../../utils/cameraControl";
import { ILocalProducer } from "../../models/reducers/localProducer";
import { IMessageState } from "../../models/reducers/message";
import { IUser } from "../../models/reducers/comsumers";
import { recorderVideo, stopRecording } from "../../utils/recorderVideo";
import { IconFAPro } from "../../components/IconFAPro/IconFAPro";

const menu = (
    <Menu>
        <Menu.Item
            onClick={() => {
                if (!document.fullscreenElement) {
                    document.documentElement.requestFullscreen();
                } else {
                    if (document.exitFullscreen) {
                        document.exitFullscreen();
                    }
                }
            }}
        >
            Ton mn hnh
        </Menu.Item>
    </Menu>
);

interface Props {
    setModalSettingVisible: Function;
    micOpen: boolean;
    videoOpen: boolean;
    setVideoOpen: Function;
    setMicOpen: Function;
    setSiderActive: Function;
    siderActive: string;
    localAudioInput: ILocalStreams | null;
    localProducer: Array<ILocalProducer>;
    listSrcVideo: Array<ISrcVideo>;
    getConsumersFromServer: Function;
    messageState: Array<IMessageState>;
    listUser: Array<IUser>;
}
interface State {
    showButtonDropdown: boolean;
    recordStart: boolean
}

export class HomeFooter extends Component<Props, State> {
    state = {
        showButtonDropdown: false,
        recordStart: false,
    };
    timer: any = null;

    onOpenSharescreen = async () => {
        openShareScreen();
    };

    onOpenCamera = async (value: boolean) => {
        const { localProducer } = this.props;
        const producerVideo = localProducer.filter((producer) => producer.type === "videoinput");
        const producerActive = producerVideo.find((producer) => producer.active === true) || producerVideo[0];
        if (!producerActive) {
            return;
        }
        if (!producerActive.producer) {
            console.log("create");
            openLocalCamera(localProducer);
        } else if (producerActive.producer && value === true) {
            producerActive.producer.producer.resume();
            console.log("remuse", producerActive.producer);
        } else {
            producerActive.producer.producer.pause();
            console.log("pause");
        }
        await this.props.setVideoOpen(value);
    };

    onOpenMic = async (value: boolean) => {
        const { localProducer } = this.props;
        const producerVideo = localProducer.filter((producer) => producer.type === "audioinput");
        const producerActive = producerVideo.find((producer) => producer.active === true) || producerVideo[0];
        if (!producerActive) {
            return;
        }
        if (!producerActive.producer) {
            console.log("create");
            openMic(localProducer);
        } else if (producerActive.producer && value === true) {
            producerActive.producer.producer.resume();
            console.log("remuse", producerActive.producer);
        } else {
            producerActive.producer.producer.pause();
            console.log("pause");
        }
        await this.props.setMicOpen(value);
    };

    onRecord = async () => {

        var minutesLabel = document.getElementById("minutes-count");
        var secondsLabel = document.getElementById("seconds-count");
        if (!this.state.recordStart) {
            console.log("Start");
            const { localProducer } = this.props;

            const audiodeviceId = localProducer.filter(item => item.active && item.type === "audioinput")[0]?.deviceId
            const audioStream = await navigator.mediaDevices.getUserMedia({
                audio: {
                    deviceId: audiodeviceId ? { exact: audiodeviceId } : undefined,
                },
            });
            // @ts-ignore
            const screenStream: any = await navigator.mediaDevices.getDisplayMedia({
                video: true,
                audio: true,
            });
            var totalSeconds = 0;
            this.timer = setInterval(setTime, 1000);
            audioStream.getAudioTracks().forEach((track) => {
                screenStream.addTrack(track);
            });

            console.log(screenStream.getAudioTracks().forEach((track: any) => {
                console.log(track);
            }));
            recorderVideo(screenStream);
        } else {
            console.log("Stop");

            if (secondsLabel && minutesLabel) {
                secondsLabel.innerHTML = '';
                minutesLabel.innerHTML = '';
            }
            clearInterval(this.timer);
            stopRecording();
        }

        function setTime() {
            ++totalSeconds;
            if (secondsLabel && minutesLabel) {
                console.log(pad(totalSeconds % 60) + ":" + pad(Math.floor(totalSeconds / 60)));

                secondsLabel.innerHTML = pad(totalSeconds % 60);
                minutesLabel.innerHTML = pad(Math.floor(totalSeconds / 60)) + ":";
            }
        }

        function pad(val: Number) {
            var valString = val + "";
            if (valString.length < 2) {
                return "0" + valString;
            } else {
                return valString;
            }
        }
        this.setState({
            recordStart: !this.state.recordStart
        })

    }



    render() {
        const { showButtonDropdown, recordStart } = this.state;
        const { micOpen, videoOpen, siderActive, messageState, listUser } = this.props;
        return (
            <div className="home-footer">
                <ResizeObserver
                    onResize={(rect) => {
                        if (rect.width < 769) {
                            this.setState({
                                showButtonDropdown: true,
                            });
                        } else {
                            this.setState({
                                showButtonDropdown: false,
                            });
                        }
                    }}
                />

                <div className="wrapper-left">
                    <Tooltip title="Chn camera">
                        <button onClick={() => this.props.setModalSettingVisible(true)}>
                            <IconFAPro name="fal fa-cog" style={{}} />
                        </button>
                    </Tooltip>
                    {!showButtonDropdown && (
                        <>
                            <div style={{ width: 1, height: 16, background: "#dadce0", margin: "0 6px" }}></div>
                            <div className="room-name">{window.roomClient.roomname}</div>
                        </>
                    )}
                </div>
                <div style={{ flex: 1, justifyContent: "center" }}>
                    {micOpen ? (
                        <Tooltip title="Tt micro">
                            <button className="circle" onClick={() => this.onOpenMic(false)}>
                                <IconFAPro style={{}} name="fal fa-microphone" />
                            </button>
                        </Tooltip>
                    ) : (
                        <Tooltip title="Bt micro">
                            <button className="circle" onClick={() => this.onOpenMic(true)}>
                                <IconFAPro style={{}} name="fal fa-microphone-slash" />
                            </button>
                        </Tooltip>
                    )}
                    {videoOpen ? (
                        <Tooltip title="Tt camera">
                            <button className="circle" onClick={() => this.onOpenCamera(false)}>
                                <IconFAPro style={{}} name="fal fa-video" />
                            </button>
                        </Tooltip>
                    ) : (
                        <Tooltip title="Bt camera">
                            <button className="circle" onClick={() => this.onOpenCamera(true)}>
                                <IconFAPro style={{}} name="fal fa-video-slash" />
                            </button>
                        </Tooltip>
                    )}
                    <Tooltip title="Share screen">
                        <button className="circle" onClick={() => this.onOpenSharescreen()}>
                            <IconFAPro style={{}} name="fal fa-desktop" />
                        </button>
                    </Tooltip>
                    {
                        !recordStart ?
                            <Tooltip title="Record start">
                                <button className="circle" onClick={() => this.onRecord()}>
                                    <IconFAPro style={{ fontSize: 18 }} name="fal fa-play" />
                                </button>
                            </Tooltip>
                            :
                            <Tooltip title="Record stop">
                                <button style={{ fontSize: 12, background: 'tomato' }} className="circle" onClick={() => this.onRecord()}>
                                    <IconFAPro style={{ fontSize: 18 }} name="fal fa-stop" />
                                </button>
                            </Tooltip>
                    }
                    <Dropdown overlay={menu} trigger={["click"]}>
                        <button className="circle">
                            <IconFAPro style={{}} name="fal fa-ellipsis-v-alt" />
                        </button>
                    </Dropdown>
                    <Tooltip title="Ri khi cuc gi">
                        <button className="end-call">
                            <IconFAPro style={{}} name="fal fa-phone-alt" />
                        </button>
                    </Tooltip>
                </div>
                <div>
                    {/* {showButtonDropdown ? (
                        <Dropdown
                            trigger={["click"]}
                            overlay={
                                <Menu className="menu-footer">
                                    <Menu.Item key="1" icon={<IconFAPro style={{}} name="fal fa-info-circle" />}>
                                        Chi tit v cuc hp
                                    </Menu.Item>
                                    <Menu.Item key="2" icon={<IconFAPro style={{ color: "#fff" }} name="fal fa-users" />} onClick={() => {
                                        if (siderActive !== "users") {
                                            this.props.setSiderActive("users");
                                        } else {
                                            this.props.setSiderActive("none");
                                        }
                                    }}>
                                        Hin th tt c mi ngi
                                    </Menu.Item>
                                    <Menu.Item key="3" icon={<IconFAPro style={{ color: "#fff"  }} name="fal fa-comment-alt-dots" />} onClick={() => {
                                        if (siderActive !== "chat") {
                                            this.props.setSiderActive("chat");
                                        } else {
                                            this.props.setSiderActive("none");
                                        }
                                    }}>
                                        Tr chuyn vi mi ngi
                                    </Menu.Item>
                                </Menu>
                            }
                        >
                            <button style={{ margin: 0, width: 30 }}>
                                <IconFAPro style={{ fontSize: "25px" }} name="fal fa-angle-up" />
                            </button>
                        </Dropdown>
                    ) : ( */}
                    <>
                        {/* <Tooltip placement="topRight" title="Chi tit v cuc hp">
                                <button>
                                    <IconFAPro style={{}} name="fal fa-info-circle" />
                                </button>
                            </Tooltip> */}
                        {
                            !showButtonDropdown &&
                            <Tooltip placement="topRight" title="Hin th tt c mi ngi">
                                <Badge
                                    size={"default"}
                                    count={listUser.length}
                                    overflowCount={99}
                                    style={{ backgroundColor: "#52c41a" }}
                                >
                                    <button
                                        onClick={() => {
                                            if (siderActive !== "users") {
                                                this.props.setSiderActive("users");
                                            } else {
                                                this.props.setSiderActive("none");
                                            }
                                        }}
                                    >
                                        <IconFAPro style={{ color: "#fff" }} name="fal fa-users" />
                                    </button>
                                </Badge>
                            </Tooltip>
                        }
                        <Tooltip placement="topRight" title="Tr chuyn vi mi ngi">
                            <Badge size={"default"} count={messageState.length} overflowCount={10}>
                                <button
                                    onClick={() => {
                                        if (siderActive !== "chat") {
                                            this.props.setSiderActive("chat");
                                        } else {
                                            this.props.setSiderActive("none");
                                        }
                                    }}
                                >
                                    <IconFAPro style={{ color: "#fff" }} name="fal fa-comment-alt-dots" />
                                </button>
                            </Badge>
                        </Tooltip>
                    </>
                    {/* )} */}
                </div>
            </div>
        );
    }
}

const mapStateToProps = (state: any) => ({
    listUser: state.listUser,
    micOpen: state.micOpen,
    videoOpen: state.videoOpen,
    siderActive: state.siderActive,
    localAudioInput: state.localAudioInput, // Lu audio  c chn
    localProducer: state.localProducer, // Lu video  c chn
    listSrcVideo: state.listSrcVideo,
    messageState: state.messageState,
});

const mapDispatchToProps = (dispatch: any) => {
    return {
        setModalSettingVisible: (value: boolean) => dispatch(setModalSettingVisible(value)),
        setVideoOpen: (value: boolean) => dispatch(setVideoOpen(value)),
        setMicOpen: (value: boolean) => dispatch(setMicOpen(value)),
        setSiderActive: (value: string) => dispatch(setSiderActive(value)),
        getConsumersFromServer: (roomname: string) => dispatch(getConsumersFromServer(roomname)),
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(HomeFooter);
