import { Button } from "antd";
import React, { Component } from "react";

interface Props {
    producer: any;
}
interface State {
    mute: boolean
}

class ProducerToConsumerAudio extends Component<Props, State> {
    audioRef: HTMLAudioElement | null;

    constructor(props: Props) {
        super(props);
        this.audioRef = null;
        this.state = {
            mute: false
        }
    }

    componentDidMount = async () => {
        const { producer } = this.props;
        console.log("componentDidMount", producer.producerId);
        const { consumer, stream } = await this.producerToConsumer(producer.producerId);
        if (this.audioRef) {
            this.stopStream();
            this.audioRef.srcObject = stream;
        }
    };

    shouldComponentUpdate = (nextProps: Props, nextState: State) => {
        const { producer } = this.props;
        console.log("shouldComponentUpdate", producer.producerId !== nextProps.producer.producerId);
        console.log("shouldComponentUpdate nextState", nextState);
        if (producer.producerId !== nextProps.producer.producerId) {
            return true;
        } else {
            return false;
        }
    };

    componentDidUpdate = async (prevProps: Props) => {
        const { producer } = this.props;
        console.log("componentDidUpdate", producer.producerId !== prevProps.producer.producerId);
        if (producer.producerId !== prevProps.producer.producerId) {
            this.stopStream();
            const { consumer, stream } = await this.producerToConsumer(producer.producerId);
            if (this.audioRef) {
                this.audioRef.srcObject = stream;
            }
        }
    };

    componentWillUnmount = () => {
        const { producer } = this.props;
        this.stopStream();
        console.log("componentWillUnmount", producer.producerId);
    };

    stopStream = () => {
        if (this.audioRef) {
            const stream: any = this.audioRef.srcObject;
            if (stream) {
                const tracks = stream.getTracks();
                tracks.forEach((track: any) => {
                    track.stop();
                });
            }
        }
    };

    producerToConsumer = async (producerId: string) => {
        const consumer = await window.roomClient.getConsumerFromProducerId(producerId);
        const stream = new MediaStream();
        stream.addTrack(consumer.track);
        return {
            consumer,
            stream,
        };
    };

    onMute = () => {
        const btnOnMic = document.getElementById("btn-on-mic")
        const btnOffMic = document.getElementById("btn-off-mic")
        if (btnOnMic && btnOffMic) {
            if (this.audioRef) {
                this.audioRef.muted = !this.audioRef?.muted
            }
            if (btnOnMic.className.indexOf("hidden") !== -1) {
                btnOnMic.className = "btn-audio";
                btnOffMic.className = "btn-audio hidden";
            } else {
                btnOnMic.className = "btn-audio hidden";
                btnOffMic.className = "btn-audio";
            }
        }
    }

    render() {
        return (
            <div>
                <button
                    className={"btn-audio hidden"}
                    id="btn-on-mic"
                    onClick={this.onMute}
                >
                    <i className="fal fa-volume-slash"></i>
                </button>
                <button
                    className={"btn-audio "}
                    id="btn-off-mic"
                    onClick={this.onMute}
                >
                    <i className="fal fa-volume-up"></i>
                </button>
                <audio
                    autoPlay={true}
                    muted={false}
                    style={{ display: "none", width: "100%", height: "100%", objectFit: "contain" }}
                    ref={(ref) => {
                        this.audioRef = ref;
                    }}
                ></audio>
            </div>
        );
    }
}

export default ProducerToConsumerAudio;
