export const ACTION_TYPES = {
    SET_VIDEO_OPEN: "SET_VIDEO_OPEN",
};

interface IAction {
    type: string;
    payload: any;
}

const initialize: boolean = false;

const videoOpenReducer = (state = initialize, action: IAction) => {
    switch (action.type) {
        case ACTION_TYPES.SET_VIDEO_OPEN:
            state = action.payload;
            return state;
        default:
            return state;
    }
};

const setVideoOpen = (data: boolean) => {
    return {
        type: ACTION_TYPES.SET_VIDEO_OPEN,
        payload: data,
    };
};

export { videoOpenReducer, setVideoOpen };
