import { Col, Row } from "antd";
import React, { Component, PureComponent } from "react";
import { IComsumer } from "../../models/reducers/comsumers";
import { currentMainProducers } from "../../models/variables/mainProducer";
import ProducerToConsumer from "../ProducerToConsumer/ProducerToConsumer";
import ProducerToConsumerAudio from "../ProducerToConsumer/ProducerToConsumerAudio";

interface Props {
    userData: IComsumer;
}
interface State {}

class UserComponent extends PureComponent<Props, State> {
    state = {};

    render() {
        const { userData } = this.props;
        const { listProducer } = userData;
        const audio = listProducer.filter((producer) => producer.producerType === "audio");
        const video = listProducer.filter((producer) => producer.producerType !== "audio");

        return (
            <Col className="col-user-component" span={video.length <= 1 ? 12 : 24}>
                <div className="wrapper">
                    <div
                        className="user-name"
                        style={userData.userId === window.roomClient.localUser.id ? { color: "orange" } : {}}
                    >
                        {userData.username}
                        {audio && audio.length > 0 && userData.userId !== window.roomClient.localUser.id ? (
                            <ProducerToConsumerAudio producer={audio[0]} />
                        ) : null}
                    </div>

                    <Row className="row-wrapper-video">
                        {video.length > 0 ? (
                            video.map((producer, index) => (
                                <Col className="col-wrapper-video" span={video.length <= 1 ? 24 : 12}>
                                    <ProducerToConsumer
                                        key={index}
                                        producer={producer}
                                    />
                                </Col>
                            ))
                        ) : (
                            <Col span={24} className="col-wrapper-video ">
                                <div className="wrapper-video video-empty">Khng c video</div>
                            </Col>
                        )}
                    </Row>
                </div>
            </Col>
        );
    }
}

export default UserComponent;
