import { store } from "../redux/store";
import { StreamAxiosInstance } from "../utils/setupAxiosInterceptors";
import { comsumersToMediaStreams, setConsumers } from "./consumersReducer";
import { setListSrcVideo } from "./listSrcVideo";

export const getConsumersFromServer: any = (roomname: string) => async (dispatch: any) => {
    const response = await StreamAxiosInstance.post("/call/getConsumerUser", { roomname });
    store.dispatch(setConsumers(response.data));
    const data = await comsumersToMediaStreams(response.data)
    store.dispatch(setListSrcVideo(data))
};
