import { ISrcVideo } from "../models/reducers/listSrcVideo";
import { ILocalProducer } from "../models/reducers/localProducer";
import { ILocalStreams } from "../models/reducers/localStreams";
import { getConsumersFromServer } from "../reducers/consumer";
import { setLocalProducer } from "../reducers/localProducerReducer";
import { getUserInRoom } from "../reducers/usersReducer";
import { store } from "../redux/store";
let activeVideoProducer: any = null;
let activeMicProducer: any = null;
export const openLocalCamera = async (ILocalProducer: Array<ILocalProducer>) => {
    const producerVideo = ILocalProducer.filter((producer) => producer.type === "videoinput");
    const producerActive = producerVideo.find((producer) => producer.active === true) || producerVideo[0];
    if (!producerActive) {
        return;
    }
    if (activeVideoProducer) {
        await window.roomClient.localUser.closeProducer(activeVideoProducer.producer.id);
    }
    const roomclient = window.roomClient;
    const localUser = roomclient.localUser;
    const mediaConstraints: any = {
        video: {
            deviceId: producerActive.deviceId ? { exact: producerActive.deviceId } : undefined,
        },
        audio: false,
    };
    const mediaDevices = navigator.mediaDevices;
    const stream = await mediaDevices.getUserMedia(mediaConstraints);
    const track = stream.getVideoTracks()[0];
    const producer = await localUser?.produce(
        roomclient.routerCapabilities,
        roomclient.producerTransport,
        track,
        "camera"
    );
    activeVideoProducer = { producer, deviceId: producerActive.deviceId };
    producerActive.active = true;
    producerActive.producer = producer;
    await store.dispatch(setLocalProducer(ILocalProducer));
    await store.dispatch(getUserInRoom());
};

