export const openShareScreen = async () => {
    const roomclient = window.roomClient;
    const localUser = roomclient.localUser;
    const mediaConstraints = { video: true, audio: false };
    const mediaDevices: any = navigator.mediaDevices;
    const stream = await mediaDevices.getDisplayMedia(mediaConstraints);
    const track = stream.getVideoTracks()[0];
    const producer = await localUser?.produce(
        roomclient.routerCapabilities,
        roomclient.producerTransport,
        track,
        "screenshare"
    );
    await store.dispatch(getUserInRoom());
};

export const closeLocalCamera = async (listSrcVideo: Array<ISrcVideo>) => {
    if (listSrcVideo.filter((i) => i.producerType === "video" && i.type === "localUser")[0]) {
        await window.roomClient.localUser.closeProducer(
            listSrcVideo.filter((i) => i.producerType === "video" && i.type === "localUser")[0].producerId
        );
    }
};
