export const ACTION_TYPES = {
    SET_MODAL_SETTING_VISIBLE: "SET_MODAL_SETTING_VISIBLE",
};

interface IAction {
    type: string;
    payload: any;
}

const initialize: boolean = false;

const modalSettingVisibleReducer = (state = initialize, action: IAction) => {
    switch (action.type) {
        case ACTION_TYPES.SET_MODAL_SETTING_VISIBLE:
            state = action.payload;
            return state;

        default:
            return state;
    }
};

const setModalSettingVisible = (data: boolean) => {
    return {
        type: ACTION_TYPES.SET_MODAL_SETTING_VISIBLE,
        payload: data,
    };
};

export { modalSettingVisibleReducer, setModalSettingVisible };
