import { ILocalStreams } from "../models/reducers/localStreams";

interface IAction {
    type: string;
    payload: any;
}

const ACTION_TYPES = {
    SET_LOCAL_AUDIO_INPUT: "SET_LOCAL_AUDIO_INPUT",
};

const initialstate: ILocalStreams | null = null;

const localAudioInputReducer = (state = initialstate, action: IAction) => {
    switch (action.type) {
        case ACTION_TYPES.SET_LOCAL_AUDIO_INPUT:
            state = action.payload;
            return state;
        default:
            return state;
    }
};

const setLocalAudioInput = (value: ILocalStreams | null) => {
    return {
        type: ACTION_TYPES.SET_LOCAL_AUDIO_INPUT,
        payload: value,
    };
};

export { localAudioInputReducer, setLocalAudioInput };