import React, { Component } from "react";
import HomePage from "./pages/HomePage";
import ReactDOM from "react-dom";
import { Provider } from "react-redux";
import ErrorBoundry from "./components/ErrorBoundry/ErrorBoundry";
import "antd/dist/antd.css";
import "./style/App.scss";
import "./style/home-page.scss";
import "./style/home-footer.scss";
import "./style/home-main.scss";
import "./style/modal-setting-camera.scss";
import "./style/list-user.scss";
import "./style/producer-consumer.scss";
import "./style/home-page-chat-sider.scss";

import "./style/home-chat-slider.scss";
import "./style/login-page.scss";
import "./style/smallview.scss"

import { serverConfig } from "./models/variables/serverConfig";
import { setupAxiosInterceptors } from "./utils/setupAxiosInterceptors";
import { getDevices } from "./utils/getDecices";
import { store } from "./redux/store";
import { Spin } from "antd";
import { LoginPage } from "./pages/LoginPage";
import RedirectPage from "./pages/RedirectPage";

interface Props { }
interface State {
    loadDevices: boolean;
    page: string;
}

declare global {
    interface Window {
        test: any;
    }
}

declare global {
    interface Window {
        ReactDOM: any;
        React: any;
        roomClient: any;
        MediaRecorder: any;
        RecordRTC: any;
        config: any;
    }
}
class App extends Component<Props, State> {
    constructor(props: Props) {
        super(props);
        this.setupDevicesDefault();
        this.setGlobalWindowVariable();
        this.getConfig();
        setupAxiosInterceptors(() => { });
        this.state = { loadDevices: true, page: "login" };
    }
    setGlobalWindowVariable = () => {
        window.React = React;
        window.ReactDOM = ReactDOM;
        window.MediaRecorder = window.MediaRecorder;
        window.RecordRTC = window.RecordRTC;
        window.config = window.config;
    };

    getConfig = () => {
        const scStr: any = window.config;
        let serverCfg = null;
        let localStorageServerConfig: string | any = localStorage.getItem("serverConfig");
        if (localStorageServerConfig) {
            serverCfg = JSON.parse(localStorageServerConfig);
        }
        if (!serverCfg) {
            return;
        }
        if (window.location.protocol === "http:") {
            serverConfig.scuServerUrl = serverCfg.HTTP_SCU_SERVER;
            serverConfig.risServerUrl = serverCfg.HTTP_RIS_SERVER;
            serverConfig.socketServerUrl = serverCfg.HTTP_SOCKET_SERVER;
        } else {
            serverConfig.scuServerUrl = serverCfg.HTTPS_SCU_SERVER;
            serverConfig.risServerUrl = serverCfg.HTTPS_RIS_SERVER;
            serverConfig.socketServerUrl = serverCfg.HTTPS_SOCKET_SERVER;
        }
    };

    setupDevicesDefault = async () => {
        await getDevices();
        console.log(12312);

        this.setState({ loadDevices: false });
    };



    render() {
        const { loadDevices, page } = this.state;
        return loadDevices ? (
            <div style={{ width: '100%', height: '100vh', display: 'flex', justifyContent: 'center', alignItems: 'center' }}>
                <Spin size="large" tip="Loading devices"></Spin>
            </div>
        ) : (
            <Provider store={store}>
                <ErrorBoundry />
                <RedirectPage />
            </Provider>
        );
    }
}

export default App;
