import { Col, Row } from "antd";
import React, { Component } from "react";
import { connect } from "react-redux";
import { IComsumer, IUser } from "../../models/reducers/comsumers";
import { ISrcVideo } from "../../models/reducers/listSrcVideo";
import UserComponent from "../User/UserComponent";
import VideoComponent from "../VideoComponent/VideoComponent";

interface Props {
    listSrcVideo: Array<ISrcVideo>;
    consumers: Array<IComsumer>;
    listUser: Array<IUser>;
}
interface State {}

export class ListUserCamera extends Component<Props, State> {
    state = {};

    render() {
        const { listUser } = this.props;
        listUser.sort(function (a, b) {
            return (
                b.listProducer.filter((producer) => producer.producerType === "video").length -
                a.listProducer.filter((producer) => producer.producerType === "video").length
            );
        });
        return (
            <Row className="row-list-user">
                {listUser.map((user, index) => {
                    return <UserComponent key={index} userData={user} />;
                })}
            </Row>
        );
    }
}

const mapStateToProps = (state: any) => ({
    listUser: state.listUser,
    listSrcVideo: state.listSrcVideo,
    consumers: state.consumers,
});

const mapDispatchToProps = {};

export default connect(mapStateToProps, mapDispatchToProps)(ListUserCamera);
