import React, { Component } from 'react'
import { connect } from 'react-redux'
import { IComsumer } from '../../models/reducers/comsumers';
import { ISrcVideo } from '../../models/reducers/listSrcVideo'
import { setSiderActive } from '../../reducers/ui/siderActive';
import ListUserCamera from './ListUserCamera';
import ListUserDefault from './ListUserDefault';

interface Props {
    listSrcVideo: Array<ISrcVideo>
    consumers: Array<IComsumer>
    setSiderActive: Function
}
interface State {

}

export class ListUser extends Component<Props, State> {

    onSwitchMode = () => {
        this.props.setSiderActive("none")
    }
    render() {
        return (
            <div className="list-user">
                <div className="header">
                    <div className="header-title">Mi ngi</div>
                    <div className="header-btn">
                        <button onClick={this.onSwitchMode}><i className="fal fa-times"></i></button>
                    </div>
                </div>
                <div className="wrapper-list-user-camera">
                    <ListUserCamera />
                    {/* <ListUserDefault /> */}
                </div>
            </div>
        )
    }
}

const mapStateToProps = (state: any) => ({
    listSrcVideo: state.listSrcVideo,
    consumers: state.consumers,
})

const mapDispatchToProps = (dispatch: any) => {
    return {
        setSiderActive: (value: string) => dispatch(setSiderActive(value))
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(ListUser)
