import { ILocalStreams } from "./../models/reducers/localStreams";
import { store } from "../redux/store";
import { setLocalStreams } from "../reducers/localStreams";
import { setLocalProducer } from "../reducers/localProducerReducer";
import { ILocalProducer } from "../models/reducers/localProducer";

export const getDevices = async () => {
    var constraints = { audio: true, video: true };
    const data = await window.navigator.mediaDevices.getUserMedia(constraints);
    const devices = await window.navigator.mediaDevices.enumerateDevices();
    const deviceList = [];
    for (let index = 0; index < devices.length; index++) {
        const deviceInfo = devices[index];
        const deviceKind = deviceInfo.kind;
        const deviceName = deviceInfo.label;
        const deviceId = deviceInfo.deviceId;

        if (deviceKind === "videoinput" || deviceKind === "audioinput") {
            const localDevice: ILocalProducer = {
                name: deviceName,
                active: deviceId === "default" ? true : false,
                type: deviceKind,
                deviceId,
                producer: null,
            };
            deviceList.push(localDevice);
        }
    }

    store.dispatch(setLocalProducer(deviceList));
};
