import { ISrcVideo } from "../models/reducers/listSrcVideo";


interface IAction {
    type: string;
    payload: any;
}

const ACTION_TYPES = {
    LIST_SRC_VIDEO: "LIST_SRC_VIDEO",
};

const initialstate: Array<ISrcVideo> = [];

const listSrcVideoReducer = (state = initialstate, action: IAction) => {
    switch (action.type) {
        case ACTION_TYPES.LIST_SRC_VIDEO:
            state = action.payload;
            return state;
        default:
            return state;
    }
};

const setListSrcVideo = (value: Array<ISrcVideo>) => {
    return {
        type: ACTION_TYPES.LIST_SRC_VIDEO,
        payload: value,
    };
};

export { listSrcVideoReducer, setListSrcVideo };
