import { Modal, Select, Row, Button, Tooltip, Col } from "antd";
import React, { Component } from "react";
import { connect } from "react-redux";
import { ISrcVideo } from "../../models/reducers/listSrcVideo";
import { ILocalProducer } from "../../models/reducers/localProducer";
import { ILocalStreams } from "../../models/reducers/localStreams";
import { getConsumersFromServer } from "../../reducers/consumer";
// import { getConsumersFromServer } from '../../reducers/consumersReducer'
import { setLocalAudioInput } from "../../reducers/localAudioInput";
import { setLocalProducer } from "../../reducers/localProducerReducer";
import { setLocalVideoInput } from "../../reducers/localVideoInput";
import { setMicOpen } from "../../reducers/ui/micOpenReducer";
import { setModalSettingVisible } from "../../reducers/ui/modalSettingVisibleReducer";
import { setVideoOpen } from "../../reducers/ui/videoOpenReducer";
import { openLocalCamera, openMic } from "../../utils/cameraControl";
import { getDevices } from "../../utils/getDecices";
const { Option } = Select;
interface Props {
    modalSettingVisible: boolean;
    setModalSettingVisible: Function;
    localStreams: Array<ILocalStreams>;
    getConsumersFromServer: Function;
    setLocalVideoInput: Function;
    setLocalAudioInput: Function;
    localAudioInput: ILocalStreams | null;
    localVideoInput: ILocalStreams | null;
    micOpen: boolean;
    videoOpen: boolean;
    setVideoOpen: Function;
    setMicOpen: Function;
    listSrcVideo: Array<ISrcVideo>;
    localProducer: Array<ILocalProducer>;
    setLocalProducer: Function;
}
interface State {}

export class ModalSetting extends Component<Props, State> {
    handleOk = async () => {
        const { videoOpen, micOpen, localProducer } = this.props;
        console.log(videoOpen);
        if (videoOpen) {
            openLocalCamera(localProducer);
        }
        if (micOpen) {
            openMic(localProducer);
        }
        this.props.setModalSettingVisible(false);
    };

    handleCancel = () => {
        this.props.setModalSettingVisible(false);
    };

    onChangeVideoDevice = (value: string) => {
        const { localProducer } = this.props;
        localProducer.map((producer) => {
            if (producer.deviceId === value) {
                producer.active = true;
            } else {
                producer.active = false;
            }
        });
        this.props.setLocalProducer(localProducer);
    };
    onChangeAudioDevice = (value: string) => {
        const { localProducer } = this.props;
        localProducer.map((producer) => {
            if (producer.deviceId === value) {
                producer.active = true;
            } else {
                producer.active = false;
            }
        });
        this.props.setLocalProducer(localProducer);
    };
    render() {
        const {
            modalSettingVisible,
            localStreams,
            localVideoInput,
            localAudioInput,
            micOpen,
            videoOpen,
            localProducer,
        } = this.props;
        const videosInput: Array<ILocalProducer> = [];
        const audiosInput: Array<ILocalProducer> = [];
        localProducer.forEach((stream) => {
            if (stream.type === "audioinput") {
                audiosInput.push(stream);
            } else if (stream.type === "videoinput") {
                videosInput.push(stream);
            }
        });
        return (
            <Modal
                title="Cu hnh Camera v Micro"
                closable={false}
                width={420}
                className="modal-setting-camera"
                visible={modalSettingVisible}
                footer={[
                    <Button key="submit" type="primary" onClick={this.handleOk}>
                        Lu
                    </Button>,
                    <Button key="close" type="primary" danger onClick={this.handleCancel}>
                        ng
                    </Button>,
                ]}
            >
                <Row className="wrapper-input">
                    <Col span={24}>Micro</Col>
                    <Col span={24}>
                        <Select
                            defaultValue={audiosInput.find((device) => device.deviceId === "default")?.deviceId}
                            style={{ width: "100%" }}
                            onChange={this.onChangeAudioDevice}
                        >
                            {audiosInput.map((audio, index) => {
                                return (
                                    <Option key={index} value={audio.deviceId}>
                                        {audio.name}
                                    </Option>
                                );
                            })}
                        </Select>
                    </Col>
                </Row>
                <Row style={{ marginTop: 20 }} className="wrapper-input">
                    <Col span={24}>Camera</Col>
                    <Col span={24}>
                        <Select
                            defaultValue={videosInput.find((device) => device.deviceId === "default")?.deviceId}
                            style={{ width: "100%" }}
                            onChange={this.onChangeVideoDevice}
                        >
                            {videosInput.map((video, index) => {
                                return (
                                    <Option key={index} value={video.deviceId}>
                                        {video.name}
                                    </Option>
                                );
                            })}
                        </Select>
                    </Col>
                </Row>
            </Modal>
        );
    }
}

const mapStateToProps = (state: any) => ({
    localProducer: state.localProducer,
    modalSettingVisible: state.modalSettingVisible,
    localStreams: state.localStreams,
    localAudioInput: state.localAudioInput, // Lu audio  c chn
    localVideoInput: state.localVideoInput, // Lu video  c chn
    micOpen: state.micOpen,
    videoOpen: state.videoOpen,
    listSrcVideo: state.listSrcVideo,
});

const mapDispatchToProps = (dispatch: any) => {
    return {
        setLocalProducer: (data: Array<ILocalProducer>) => dispatch(setLocalProducer(data)),
        setModalSettingVisible: (value: boolean) => dispatch(setModalSettingVisible(value)),
        getConsumersFromServer: (roomname: string) => dispatch(getConsumersFromServer(roomname)),
        setLocalAudioInput: (data: ILocalStreams | null) => dispatch(setLocalAudioInput(data)),
        setLocalVideoInput: (data: ILocalStreams | null) => dispatch(setLocalVideoInput(data)),
        setVideoOpen: (value: boolean) => dispatch(setVideoOpen(value)),
        setMicOpen: (value: boolean) => dispatch(setMicOpen(value)),
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(ModalSetting);
