import { combineReducers } from "redux";
import { authReducer } from "../reducers/authenticateReducer";
import { errorBoundryReducer } from "../reducers/errorBoudryReducer";
import { listSrcVideoReducer } from "../reducers/listSrcVideo";
import { localAudioInputReducer } from "../reducers/localAudioInput";
import { localStreamsReducer } from "../reducers/localStreams";
import { localVideoInputReducer } from "../reducers/localVideoInput";
import { messageReducer } from "../reducers/messageReducer";
import { micOpenReducer } from "../reducers/ui/micOpenReducer";
import { modalSettingVisibleReducer } from "../reducers/ui/modalSettingVisibleReducer";
import { siderActiveReducer } from "../reducers/ui/siderActive";
import { videoOpenReducer } from "../reducers/ui/videoOpenReducer";
import { consumersReducer } from "../reducers/consumersReducer";
import { usersReducer } from "../reducers/usersReducer";
import { localProducerReducer } from "../reducers/localProducerReducer";
import { pageReducer } from "../reducers/pageReducer";

const rootReducer = combineReducers({
    errorBoundryState: errorBoundryReducer,
    isAuthenticate: authReducer,
    messageState: messageReducer,
    listSrcVideo: listSrcVideoReducer,
    localStreams: localStreamsReducer,
    modalSettingVisible: modalSettingVisibleReducer,// Lu danh sch tt c cc Devices ca my
    localAudioInput: localAudioInputReducer,// Lu audio  c chn
    localVideoInput: localVideoInputReducer,// Lu video  c chn
    videoOpen: videoOpenReducer, // cp quyn m video,
    micOpen: micOpenReducer, // cp quyn m audio,
    siderActive: siderActiveReducer,
    consumers: consumersReducer,
    listUser: usersReducer,
    localProducer: localProducerReducer,
    pageReducer,
});
export { rootReducer };
