export const ACTION_TYPES = {
    SET_SIDER_ACTIVE: "SET_SIDER_ACTIVE",
};

interface IAction {
    type: string;
    payload: any;
}

const initialize: string = "users"; // "none" , "chat", "users"

const siderActiveReducer = (state = initialize, action: IAction) => {
    switch (action.type) {
        case ACTION_TYPES.SET_SIDER_ACTIVE:
            state = action.payload;
            return state;

        default:
            return state;
    }
};

const setSiderActive = (data: string) => {
    return {
        type: ACTION_TYPES.SET_SIDER_ACTIVE,
        payload: data,
    };
};

export { siderActiveReducer, setSiderActive };
