import { Dispatch } from "redux";
import { IComsumer } from "../models/reducers/comsumers";
import { ISrcVideo } from "../models/reducers/listSrcVideo";
import { store } from "../redux/store";
import { StreamAxiosInstance } from "../utils/setupAxiosInterceptors";
import { setListSrcVideo } from "./listSrcVideo";

interface IAction {
    type: string;
    payload: any;
}

const ACTION_TYPES = {
    SET_CONSUMERS: "SET_CONSUMERS",
};


const initialstate: Array<IComsumer> = [];

const consumersReducer = (state = initialstate, action: IAction) => {
    switch (action.type) {
        case ACTION_TYPES.SET_CONSUMERS:
            state = action.payload;
            return [...state];
        default:
            return state;
    }
};

const setConsumers = (value: Array<IComsumer>) => {
    return {
        type: ACTION_TYPES.SET_CONSUMERS,
        payload: value,
    };
};


const comsumersToMediaStreams = async (comsumers: Array<IComsumer>) => {
    
    const listSrcVideo: Array<ISrcVideo> = []
    for (let i = 0; i < comsumers.length; i++) {
        const comsumer = comsumers[i];
        for (let j = 0; j < comsumer.listProducer.length; j++) {
            const producer = comsumer.listProducer[j];

            const roomClient = window.roomClient;

            const consumer = await roomClient.getConsumerFromProducerId(producer.producerId);
            const stream = new MediaStream();
            stream.addTrack(consumer.track);

            const data: ISrcVideo = {
                producerId: producer.producerId,
                producerType: producer.producerType,
                stream,
                type: comsumer.userId === window.roomClient.localUser.id ? "localUser" : 'remoteUser',
                active: false,
                activeMain: false,
                userId: comsumer.userId,
                consumerId: consumer.id,
                username: comsumer.username,
            }
            listSrcVideo.push(data)
        }
    }
    return listSrcVideo;
}




export {
    setConsumers,
    consumersReducer,
    // getConsumersFromServer,
    comsumersToMediaStreams
};
