export const ACTION_TYPES = {
    SET_MIC_OPEN: "SET_MIC_OPEN",
};

interface IAction {
    type: string;
    payload: any;
}

const initialize: boolean = false;

const micOpenReducer = (state = initialize, action: IAction) => {
    switch (action.type) {
        case ACTION_TYPES.SET_MIC_OPEN:
            state = action.payload;
            return state;

        default:
            return state;
    }
};

const setMicOpen = (data: boolean) => {
    return {
        type: ACTION_TYPES.SET_MIC_OPEN,
        payload: data,
    };
};

export { micOpenReducer, setMicOpen };
