import { AnyAction } from "redux";
import { ThunkAction } from "redux-thunk";
import { defaultMessageState, IMessageState } from "../models/reducers/message";
import { StreamAxiosInstance } from "../utils/setupAxiosInterceptors";

export const ACTION_TYPES = {
    SET_MESSAGE: "SET_MESSAGE",
};

interface IAction {
    type: string;
    payload: any;
}

const initializeMessage: Array<IMessageState> = defaultMessageState;

const messageReducer = (state = initializeMessage, action: IAction) => {
    switch (action.type) {
        case ACTION_TYPES.SET_MESSAGE:
            state = action.payload;
            return state;

        default:
            return state;
    }
};

const setMessage = (stream: IMessageState) => {
    return {
        type: ACTION_TYPES.SET_MESSAGE,
        payload: stream,
    };
};

export const sendMessage =async (message: string, roomname: string, userID: string) => {
    const response = await StreamAxiosInstance.post("/call/sendMessage", {
        roomname: roomname,
        message: message,
        userId: userID,
    });
    // dispatch(getMessage());
};

const getMessage = () => async (dispatch: any) => {
    const messages = await StreamAxiosInstance.post("/call/getMessageInRoom", {
        roomname: window.roomClient.roomname,
    });
    return dispatch(setMessage(messages.data));
};

export { messageReducer, setMessage, getMessage };
