import React, { Component } from "react";
import { connect } from "react-redux";
import { BubbleGroup, ChatFeed, Message } from "react-chat-ui";
import { Button, Input, Layout, message } from "antd";
import { SendOutlined } from "@ant-design/icons";
import { IMessageState } from "../../models/reducers/message";
import { StreamAxiosInstance } from "../../utils/setupAxiosInterceptors";
import { getMessage } from "../../reducers/messageReducer";
import { roomData } from "../../models/variables/roomData";
import { setSiderActive } from "../../reducers/ui/siderActive";
const { Footer, Content, Header } = Layout;
const { Search } = Input;
interface Props {
    messageState: Array<IMessageState>;
    getMessage: Function;
    setSiderActive: Function
}
interface State {
    messages: Array<any>;
}

class HomeSiderChat extends Component<Props, State> {
    messagebox: any;
    shouldComponentUpdate = (nextProp: Props) => {
        if (nextProp.messageState.length !== this.props.messageState.length) {
            return true;
        } else {
            return false;
        }
    };

    sendMessage = async (value: any) => {
        const response = await StreamAxiosInstance.post("/call/sendMessage", {
            roomname: window.roomClient.roomname,
            message: value,
            userId: window.roomClient.localUser.id,
        });
        this.props.getMessage();
        this.messagebox.setState({ value: "" });
    };
    render() {
        const messages = this.props.messageState;
        const convertMessage = [];
        for (let index = 0; index < messages.length; index++) {
            const mess = new Message({
                id: messages[index].id === window.roomClient.localUser.id ? 0 : 1,
                senderName: messages[index].name,
                message: messages[index].message,
            });
            convertMessage.push(mess);
        }

        return (
            <div className="home-sider-chat">
                <Layout style={{ width: "100%", height: "100%", borderRadius: 5, background: '#fff' }}>
                    <Header
                        style={{
                            height: 40,
                            display: "flex",
                            alignItems: "center",
                            justifyContent: "space-between",
                            color: "#fff",
                            fontSize: '14px',
                            padding: '0 10px',
                            background: '#333',
                        }}
                    >
                        <b style={{
                            marginLeft: 10, fontSize: '18px',
                        }}> {roomData.roomName}</b>

                        <Button style={{
                            color: 'tomato', fontSize: 20, display: "flex",
                            alignItems: "center", justifyContent: 'center', padding: 0
                        }} type="link" onClick={() => this.props.setSiderActive("none")}><i className="fal fa-times"></i></Button>

                    </Header>
                    <Content>
                        <ChatFeed
                            messages={convertMessage} // Array: list of message objects
                            hasInputField={false} // Boolean: use our input, or use your own
                            showSenderName // show the name of the user who sent the message
                            bubblesCentered={false} //Boolean should the bubbles be centered in the feed?
                            bubbleStyles={{
                                text: {
                                    fontSize: 13,
                                    color: '#050505'
                                },
                                chatbubble: {
                                    borderRadius: 18,
                                    padding: '7px 12px',
                                    color: '#000',
                                    marginTop: 0,
                                    marginLeft: 5,
                                    marginRight: 5,
                                    backgroundColor: '#e4e6eb',
                                    maxWidth: '80%'
                                },
                            }}
                        />
                    </Content>
                    <Footer style={{ padding: 5, borderBottomRightRadius: 5, borderBottomLeftRadius: 5, background: '#fff' }}>
                        <Search
                            ref={(ref) => {
                                this.messagebox = ref;
                            }}
                            placeholder=""
                            enterButton={<SendOutlined />}
                            size="middle"
                            onSearch={this.sendMessage}
                        />
                    </Footer>
                </Layout>
            </div>
        );
    }
}

const mapStateToProps = (state: any) => ({
    messageState: state.messageState,
});

const mapDispatchToProps = (dispatch: any) => {
    return {
        getMessage: () => dispatch(getMessage()),
        setSiderActive: (value: string) => dispatch(setSiderActive(value))
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(HomeSiderChat);
