import { Button, Input, message } from "antd";
import React, { Component } from "react";
import { connect } from "react-redux";
import { roomData } from "../models/variables/roomData";
import { EyeInvisibleOutlined, EyeTwoTone } from "@ant-design/icons";
import { store } from "../redux/store";
import { setPage } from "../reducers/pageReducer";
interface Props {}
interface State {}

export class LoginPage extends Component<Props, State> {
    state = {
        roomName: "",
        roomKey: "",
        username: "",
    };

    componentDidMount = () => {
        const url = new URL(window.location.href);
        const params = url.searchParams.get("option");
        // getDevices();
        if (params) {
            const decodeParams = decodeURIComponent(window.atob(params));
            const toJson = JSON.parse(decodeParams);
            roomData.roomName = toJson.roomName;
            roomData.roomKey = toJson.roomKey;
            roomData.username = toJson.username;
            roomData.sid = toJson.sid;
            this.setState({
                roomName: toJson.roomName,
                roomKey: toJson.roomKey ? toJson.roomKey : "",
                username: toJson.username,
            });
        } else {
            this.setState({
                loading: true,
            });
        }
    };

    onSubmit = () => {
        if (!roomData.username || roomData.username.trim() === "") {
            message.info("Vui lng nhp tn hin th!");
        } else {
            store.dispatch(setPage("home"));
        }
    };
    render() {
        return (
            <div className="login-page">
                <div className="login-wrapper">
                    <div style={{ textAlign: "center", color: "orangered", fontSize: 22, fontWeight: 600 }}>VRPACS</div>
                    <div style={{ textAlign: "center", color: "royalblue", fontSize: 16, fontWeight: 600 }}>
                        Video Conference
                    </div>
                    <div className="label">Tn phng</div>
                    <Input
                        value={this.state.roomName}
                        onChange={(e) => {
                            roomData.roomName = e.target.value;
                            this.setState({ roomName: roomData.roomName });
                        }}
                    ></Input>
                    <div className="label">M phng</div>
                    <Input.Password
                        value={this.state.roomKey}
                        onChange={(e) => {
                            roomData.roomKey = e.target.value;
                            this.setState({ roomKey: roomData.roomKey });
                        }}
                        iconRender={(visible) => (visible ? <EyeTwoTone /> : <EyeInvisibleOutlined />)}
                    />
                    <div className="label">Tn hin th</div>
                    <Input
                        value={this.state.username}
                        onChange={(e) => {
                            roomData.username = e.target.value;
                            this.setState({ username: roomData.username });
                        }}
                    ></Input>
                    <Button type="primary" block style={{ marginTop: 20, marginBottom: 10 }} onClick={this.onSubmit}>
                        Vo phng
                    </Button>
                </div>
            </div>
        );
    }
}

const mapStateToProps = (state: any) => ({});

const mapDispatchToProps = {};

export default connect(mapStateToProps, mapDispatchToProps)(LoginPage);
