import { Dispatch } from "redux";
import { defaultListUser, IUser } from "../models/reducers/comsumers";
import { ISrcVideo } from "../models/reducers/listSrcVideo";
import { store } from "../redux/store";
import { StreamAxiosInstance } from "../utils/setupAxiosInterceptors";
import { setListSrcVideo } from "./listSrcVideo";

interface IAction {
    type: string;
    payload: any;
}

const ACTION_TYPES = {
    SET_USERS: "SET_USERS",
};

const initialstate: Array<IUser> = defaultListUser;

const usersReducer = (state = initialstate, action: IAction) => {
    switch (action.type) {
        case ACTION_TYPES.SET_USERS:
            state = action.payload;
            return state;
        default:
            return state;
    }
};

const setUsers = (value: Array<IUser>) => {
    return {
        type: ACTION_TYPES.SET_USERS,
        payload: value,
    };
};

const getUserInRoom: any = () => async (dispatch: any) => {
    const userInRoom = await StreamAxiosInstance.post("/call/getConsumerUser", {
        roomname: window.roomClient.roomname,
    });
    await dispatch(setUsers(userInRoom.data));
};

export { usersReducer, setUsers, getUserInRoom };
