export const ACTION_TYPES = {
    SET_PAGE: "SET_PAGE",
};

interface IAction {
    type: string;
    payload: any;
}

const initialize: string = '';

const pageReducer = (state = initialize, action: IAction) => {
    switch (action.type) {
        case ACTION_TYPES.SET_PAGE:
            state = action.payload;
            return state;

        default:
            return state;
    }
};

const setPage = (data: string) => {
    return {
        type: ACTION_TYPES.SET_PAGE,
        payload: data,
    };
};

export { pageReducer, setPage };
