import { defaultLocalProducerList, ILocalProducer } from "../models/reducers/localProducer";
import { ILocalStreams } from "../models/reducers/localStreams";

interface IAction {
    type: string;
    payload: any;
}

const ACTION_TYPES = {
    SET_LOCAL_PRODUCER: "SET_LOCAL_PRODUCER",
};

const initialstate: Array<ILocalProducer> = defaultLocalProducerList;

const localProducerReducer = (state = initialstate, action: IAction) => {
    switch (action.type) {
        case ACTION_TYPES.SET_LOCAL_PRODUCER:
            state = action.payload;
            return state;
        default:
            return state;
    }
};

const setLocalProducer = (value: Array<ILocalProducer>) => {
    return {
        type: ACTION_TYPES.SET_LOCAL_PRODUCER,
        payload: value,
    };
};

export { localProducerReducer, setLocalProducer };
